@import url(https://fonts.googleapis.com/css?family=Noto+Sans);
.game {
  width: 80%;
  margin: 25px auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.score {
  text-align: center;
}

.color-box {
  width: 250px;
  height: 250px;
  border-radius: 35px;
}

.slider-area {
  width: 250px;
  text-align: center;
  margin-top: 35px;
}

.current-color-value {
  display: inline-block;
  width: 30px;
}

button {
  display: block;
  height: 40px;
  width: 120px;
  border-radius: 10px;
  outline: none;
  margin: auto;
}

button:hover {
  cursor: pointer;
}

input[type="range"] {
  cursor: pointer;
}
.intro-container {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.intro-text {
  width: 60%;
  font-size: 20px;
}

h2 {
  margin-top: 0;
  margin-bottom: 10px; 
}

p {
  margin: 0;
  margin-bottom: 10px;
}

a {
  text-decoration: none;
}

a:visited {
  color: inherit;
}

.action-text {
  font-weight: bold;
}
body {
  font-family: 'Noto Sans', sans-serif;
  background-color: rgb(250, 250, 250);
}

h1 {
  text-align: center;
}

.mode-button-container {
  width: 100%;
  position: absolute;
  bottom: 25px;
  display: flex;
  justify-content: center;
}
